/* Custom styles for card navigation bar */
.card-header-custom {
    background-color: #f8f9fa; /* Change background color */
    border-bottom: 1px solid #dee2e6; /* Add border bottom */
    padding: 0.75rem 1.25rem; /* Add padding */
  }
  
  .nav-pills-custom {
    display: flex; /* Use flexbox for navigation items */
  }
  
  .nav-link-custom {
    color: #007bff; /* Change link color */
    padding: 0.5rem 1rem; /* Add padding */
    border-radius: 0.25rem; /* Add border radius */
    margin-right: 0.5rem; /* Add margin between navigation items */
  }
  
  .nav-link-custom.active,
  .nav-link-custom:hover {
    background-color: #e9ecef; /* Change background color on hover/active */
    color: #0056b3; /* Change color on hover/active */
  }
  