.responsive-image {
    width: 100%;
    height: auto;
    display: block;
    max-width: 100%;
    
  }
  
  /* Styles for mobile devices */
  @media only screen and (max-width: 600px) {
    .responsive-image {
      max-width: 100%;
    }
  }
  
  /* Styles for desktop devices */
  @media only screen and (min-width: 601px) {
    .responsive-image {
        width: 100%;
        height: auto;
      max-width: 800px; /* Adjust this value based on your design */
      margin: 0 auto;
    }
  }